import React, { useEffect, useState } from "react";
import "./AdminPage.css";
import { API_URL } from "./shared/api";
import { generateInvoice } from "./shared/invoice";
import { sanitizeText, fmtINR } from "./shared/utils";
import jsPDF from "jspdf";
import "jspdf-autotable";
import * as XLSX from "xlsx";

function AdminPage() {
  const [products, setProducts] = useState([]);
  const [orders, setOrders] = useState([]);
  const [summary, setSummary] = useState([]);
  const [tab, setTab] = useState("products");
  const [editingIndex, setEditingIndex] = useState(null);
  const [editedProduct, setEditedProduct] = useState(null);

  // filters
  const [filterCategory, setFilterCategory] = useState("");
  const [filterItem, setFilterItem] = useState("");
  const [filterSubItem, setFilterSubItem] = useState("");

  useEffect(() => {
    loadData();
  }, []);

  const loadData = async () => {
    try {
      const [cat, ord, sum] = await Promise.all([
        fetch(`${API_URL}?action=getCatalog`).then((r) => r.json()),
        fetch(`${API_URL}?action=getOrders`).then((r) => r.json()),
        fetch(`${API_URL}?action=getSummary`).then((r) => r.json()),
      ]);
      setProducts(cat.data || []);
      setOrders(ord.data || []);
      setSummary(sum.data || []);
    } catch (e) {
      console.error(e);
      alert("Failed to load data. Check API URL.");
    }
  };

  const startEdit = (index) => {
    setEditingIndex(index);
    setEditedProduct({ ...products[index] });
  };

  const addNewProduct = () => {
    const newProduct = {
      Category: "New Category",
      Item: "New Item",
      "Sub Item": "Sub Item",
      "Our Price": 0,
      "Local Price": 0,
      isNew: true,
    };
    setProducts([newProduct, ...products]);
    setEditingIndex(0);
    setEditedProduct(newProduct);
  };

  const saveEdit = async () => {
    try {
      let action = editedProduct.isNew ? "addProduct" : "updateProduct";
      const response = await fetch(`${API_URL}?action=${action}`, {
        method: "POST",
        body: JSON.stringify(editedProduct),
      });
      const result = await response.json();
      if (result.success) {
        await loadData();
        setEditingIndex(null);
        setEditedProduct(null);
      } else {
        alert("Failed to save product");
      }
    } catch (err) {
      console.error(err);
      alert("Error saving product");
    }
  };

  const deleteProduct = async (product) => {
    if (!window.confirm("Delete this product?")) return;
    try {
      const response = await fetch(`${API_URL}?action=deleteProduct`, {
        method: "POST",
        body: JSON.stringify(product),
      });
      const result = await response.json();
      if (result.success) {
        await loadData();
      } else {
        alert("Failed to delete product");
      }
    } catch (err) {
      console.error(err);
      alert("Error deleting product");
    }
  };

  // Filters
  const filteredProducts = products.filter(
    (p) =>
      (!filterCategory || p.Category === filterCategory) &&
      (!filterItem || p.Item?.toLowerCase().includes(filterItem.toLowerCase())) &&
      (!filterSubItem ||
        p["Sub Item"]?.toLowerCase().includes(filterSubItem.toLowerCase()))
  );

  return (
    <div className="admin-page">
      <h2>Fireworks Admin Dashboard</h2>

      <div className="tabs">
        <button
          className={tab === "products" ? "active" : ""}
          onClick={() => setTab("products")}
        >
          Products Catalog
        </button>
        <button
          className={tab === "orders" ? "active" : ""}
          onClick={() => setTab("orders")}
        >
          Orders Summary
        </button>
        <button
          className={tab === "items" ? "active" : ""}
          onClick={() => setTab("items")}
        >
          Items Summary
        </button>
      </div>

      {/* Products Tab */}
      {tab === "products" && (
        <div>
          <h3>Products Catalog</h3>
          <div className="filters">
            <select
              value={filterCategory}
              onChange={(e) => setFilterCategory(e.target.value)}
            >
              <option value="">All Categories</option>
              {[...new Set(products.map((p) => p.Category))].map((c, i) => (
                <option key={i} value={c}>
                  {c}
                </option>
              ))}
            </select>
            <input
              placeholder="Filter Item"
              value={filterItem}
              onChange={(e) => setFilterItem(e.target.value)}
            />
            <input
              placeholder="Filter Sub Item"
              value={filterSubItem}
              onChange={(e) => setFilterSubItem(e.target.value)}
            />
            <button className="add-btn" onClick={addNewProduct}>
              + Add New Product
            </button>
          </div>

          <table className="compact-table">
            <thead>
              <tr>
                <th>Category</th>
                <th>Item</th>
                <th>Sub Item</th>
                <th>Our Price</th>
                <th>Local Price</th>
                <th>Actions</th>
              </tr>
            </thead>
            <tbody>
              {filteredProducts.length === 0 ? (
                <tr>
                  <td colSpan="6" style={{ textAlign: "center" }}>
                    No products found.
                  </td>
                </tr>
              ) : (
                filteredProducts.map((p, i) => (
                  <tr key={i}>
                    {editingIndex === i ? (
                      <>
                        <td>
                          <input
                            value={editedProduct.Category}
                            onChange={(e) =>
                              setEditedProduct({
                                ...editedProduct,
                                Category: e.target.value,
                              })
                            }
                          />
                        </td>
                        <td>
                          <input
                            value={editedProduct.Item}
                            onChange={(e) =>
                              setEditedProduct({
                                ...editedProduct,
                                Item: e.target.value,
                              })
                            }
                          />
                        </td>
                        <td>
                          <input
                            value={editedProduct["Sub Item"]}
                            onChange={(e) =>
                              setEditedProduct({
                                ...editedProduct,
                                "Sub Item": e.target.value,
                              })
                            }
                          />
                        </td>
                        <td>
                          <input
                            type="number"
                            value={editedProduct["Our Price"]}
                            onChange={(e) =>
                              setEditedProduct({
                                ...editedProduct,
                                "Our Price": e.target.value,
                              })
                            }
                          />
                        </td>
                        <td>
                          <input
                            type="number"
                            value={editedProduct["Local Price"]}
                            onChange={(e) =>
                              setEditedProduct({
                                ...editedProduct,
                                "Local Price": e.target.value,
                              })
                            }
                          />
                        </td>
                        <td>
                          <button onClick={saveEdit}>Save</button>
                          <button
                            onClick={() => {
                              setEditingIndex(null);
                              setEditedProduct(null);
                              loadData();
                            }}
                          >
                            Cancel
                          </button>
                        </td>
                      </>
                    ) : (
                      <>
                        <td>{sanitizeText(p.Category)}</td>
                        <td>{sanitizeText(p.Item)}</td>
                        <td>{sanitizeText(p["Sub Item"])}</td>
                        <td>{fmtINR(p["Our Price"])}</td>
                        <td>{fmtINR(p["Local Price"])}</td>
                        <td>
                          <button onClick={() => startEdit(i)}>Edit</button>
                          <button onClick={() => deleteProduct(p)}>
                            Delete
                          </button>
                        </td>
                      </>
                    )}
                  </tr>
                ))
              )}
            </tbody>
          </table>
        </div>
      )}

      {/* Orders and Items tabs stay same (not repeated here for brevity) */}
    </div>
  );
}

export default AdminPage;
